<template>
	<view class="content">
		<view class="Info">
			<uni-row>
				<uni-col><text class="tips">为了注册成为志愿者，请您填写下列信息：</text></uni-col>
			</uni-row>
			<uni-forms border>
				<!-- 姓名 -->
			    <uni-forms-item label="姓名:">					
			        <uni-easyinput :inputBorder="false" clearable v-model="info.name" placeholder="请输入您的名字" ></uni-easyinput>
			    </uni-forms-item>
				<!-- 性别 -->
				<uni-forms-item label="性别:">
				    <uni-data-checkbox selectedColor="#ff0000" mode="tag" v-model="value" :localdata="range" @change="onChange()"></uni-data-checkbox>
				</uni-forms-item>
				<uni-forms-item label="出生日期:">
				    <uni-datetime-picker type="date" :value="date" start="1900-6-15" end="2035-6-15" @change="dateChange"></uni-datetime-picker>
				</uni-forms-item>
				<!-- 详细地址 -->
				<!-- <uni-forms-item label="详细地址:">
						<uni-easyinput :inputBorder="false" clearable v-model="info.address" placeholder="请输入详细地址" ></uni-easyinput>
				</uni-forms-item> -->
				<!-- 身份证号 -->
				<!-- <uni-forms-item label="身份证号:">
						<uni-easyinput :inputBorder="false" clearable v-model="info.numID" placeholder="请输入身份证号" ></uni-easyinput>
				</uni-forms-item> -->
				<!-- 上传照片 -->
				<uni-forms-item label="上传照片">
						<uni-file-picker 
						    v-model="imageValue" 
						    fileMediatype="image" 
						    mode="grid" 
							:auto-upload="false"
						    @select="select" 
						    @progress="progress" 
						    @success="success" 
						    @fail="fail"
							limit="1"
						/>
				</uni-forms-item>
			</uni-forms> 		
			<!-- 选择地区 -->	
					
		</view>
		<view class="InfoConfirm">
				<button @click="finisnInfo" class="confirmButton" type="warn" >完成</button>
				<!-- <button @click="chooseImg">选择照片</button> -->
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js';//需要引入store
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default{
		data(){
			return{
				info:{
					radio:0,
					name:'',
					phone:'',
					picture:'',
					uid:'',
					birthday:'',
				},
				value: 0,
				range: [{"value": 0,"text": "男士"    },{"value": 1,"text": "女士"}],
				imageValue:[],
				show:false,
			}
		},
		computed:{
			...mapState(['token','uid','tel','pw'])
		},
		created() {
			this.info.phone=this.tel;
			this.info.uid=this.uid;
			console.log(this.token)
		},
		methods:{
			dateChange(value){
				// console.log(value)
				this.info.birthday=`${value}`;
			},
			 // 获取上传状态
			select(e){
				var that=this;
				// console.log('选择文件：',e)
				var path=e.tempFilePaths;
				var token=`Bearer ${this.token}`;
				uni.uploadFile({
					url:'https://fwwb2020-proxy-slk.tgucsdn.com/file/upload',
					header:{
						"Authorization":token,
						"Content-Type": "multipart/form-data"
					},
					filePath:path[0],
					name:'photo',
						success: (res) => {
							var data=JSON.parse(res.data)
							if(data.status==202){
								// var data=JSON.parse(res.data);
								console.log(data)
								that.info.picture=data.data.url;
							}
							console.log(that.info)
							
							
						},
						fail: (err) => {
							console.log(err)
						}
				})
			
			},
			onChange(event) {
				console.log(event.detail)
				this.info.radio=event.detail.value;
			},
			finisnInfo(){
				console.log('finish')
				var that=this;
				var token=`Bearer ${this.token}`;
				if (!/^1\d{10}$/.test(this.info.phone)) {
					uni.showToast({
						title:'手机号填写错误',
						icon:'none',
						mask:true,
						image:'../../static/img/error.png'
					})
					return
				}
				if (!this.info.birthday) {
					uni.showToast({
						mask:true,
						image:'../../static/img/error.png',
						icon: 'none',
						title: '请填写出生日期'
					});
					return;
				}
				uni.request({
					url:'https://fwwb2020-proxy-slk.tgucsdn.com/user/changeUser',
					method:'POST',
					header:{
						"content-type":"application/json",
						"Authorization":token,
					},
					data:{
						gender:that.info.radio,
						name:that.info.name,
						tel:that.info.phone,
						picture:that.info.picture,
						uid:that.info.uid,
						birthday:that.info.birthday,
						role:0,
						// password:that.pw
					},
					
					success: (res) => {
						console.log(res.data)
						if(res.data.status==200){
							uni.showToast({
								title:'完善成功',
								icon:'none',
								mask:true,
								image:'../../static/img/success.png'
							})
							var data=res.data.data;
							data.token=that.token;
							console.log(data);
							store.commit("login",data)
							setTimeout(function(){
								uni.switchTab({
									url:"../person/person"
								})
							},1000)
							// console.log(res.data)
							
						}else{
							uni.showToast({
								title:`${res.data.msg}`,
								icon:'none',
								mask:true,
								image:'../../static/img/error.png'
							})	
						}
					},
					fail: (err) => {
						uni.showToast({
							title:'完善失败！',
							icon:'none',
							mask:true,
							image:'../../static/img/error.png'
						})
					}
				})
				// this.$store.commit('volunteer')

				// uni.switchTab({
				// 	url: '/pages/volunteer/registerVolunteer'		
				// })
			}
	},
	}
</script>

<style>
	.content{
		flex-direction: column;
		padding-top: 5%;
	}
	.Info{
		flex:1;
		width: 90%;
		display: flex;
		margin: 20rpx auto;
		padding: 10rpx;
		flex-direction: column;
		border: 1px solid #eeefeb;
		border-radius: 40rpx;
	}
	.tips{
		font-size: 32rpx;
		font-weight: 600;
		margin-bottom: 40rpx;
	}
	.InfoConfirm{
		flex:1;
		width: 80%;
		margin: 20rpx auto;
	}
	.confirmButton{
		width: 100%;
	}
	.formRow{
		width: 100%;
		flex: 1;
		margin-bottom: 5px;
		/* border: 1px solid #000000; */
	}
	
	.text{
		/* height: 44px; */
		line-height: 44px;
		font-size: 14px;
		margin: 0 auto;
		color: #646566;
	}
	.Sextext{
		line-height: 20px;
		font-size: 14px;
		color: #646566;
	}
	.selectOne{
		font-size: 14px;
	}
	.selectCity{
		width: 100%;
		line-height: 44px;
		font-size: 14px;
		font-weight: 300;
		text-align: center;
		
	}
</style>
